.course-heading{
    text-align: center;
    padding: 4rem 0rem 2rem 0;
}

.course-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.course-card{
    background: #1a1919;
    padding: 1.2rem 1rem;
}

.course-card img{
    width: 100%;
}

.course-title { 
    color: #fff;
    padding: 1rem 0;
}

.course-details{
  padding-bottom: 1rem;
  font-size: 1.1rem;
  text-align: justify;
}

@media screen and (max-width: 740px){
    .course-container{
        max-width: 90%;
        margin: auto;

        grid-template-columns: 1fr;
    }
}