.mask{
    width: 100%;
    height: 100vh;
    position: relative;
}

.into-img{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.8;
}

.hero{
    height: 100%;
    width: 100%;
}

.hero .left{
  position: absolute;
  transform: translate(-50%, -50%);
  top: 75%;
  left: 25%;
  text-align: center;
}



.hero .left h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .left p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.left .btn {
    margin: 1rem 0.2rem;
}

.hero .rigth{
    position: absolute;
    transform: translate(-50%, -50%);
    top: 60%;
    left: 75%;
    text-align: center;
  }

  .hero .rigth h1{
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .rigth p{
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

@media screen and (max-width: 910px){
  
    
  .hero .left h1{
      font-size: 3rem;
  }

  .hero .left{
    left: 50%;
  }

  .rigth{
    display: none;
  }
}