.about{
    width: 100%;
    margin: 3rem 0;
    display: flex;
    align-items: start;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
}

.about .left{
    text-align: center;
    margin: auto;
    padding: 1rem;
    max-width: 350px;
}

.about .left p{
    margin: 1.2rem 0;
}




.skill-card{
    background: #1a1919;
    padding: 1rem 1rem;
}

.skill-card img{
    width: 100%;
}

.skill-title { 
    color: #fff;
}




.about .skills-container {
    max-width: 700px;
    padding: 0 1rem;
}

.skills-heading{
    display: none;
    text-align: center;
    padding: 1rem 0rem 2rem 0;
}

.skill-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}



.right .img-container{
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    position: relative;
    align-items: center;
    text-align: center;
}

.right .img{
    max-width: 90%;
    border: px solid #333;
}

.right .top{
    grid-column: 1 / span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;
}

.right .botton{
    grid-column: 3 / 11;
    grid-row: 1;
    z-index: 5;
    align-items: center;

}

@media screen and (max-width: 1050px) {
    .skills-heading{
        display: block;
    }
}